@import "src/mainColors";

.app-wrapper {
  .app-wrapper-content {
    box-sizing: border-box;
    max-width: 1000px;
    overflow: hidden;
    color: $textContentColor;
    margin: 0 auto 0 auto;
    @media (max-width: 1100px) {
      .app-wrapper-content {
        margin: 0 10px 0 60px;
      }
    }

  }
}



h3 {
  font-weight: 600;
  font-size: 20px;
}


